import moment from "moment";

export const DATEPICKER_RANGES = {
  "Hm nay": [moment(), moment()],
  "Hm qua": [moment().subtract(1, "days"), moment().subtract(1, "days")],
  "7 ngy trc": [moment().subtract(6, "days"), moment()],
  "30 ngy trc": [moment().subtract(29, "days"), moment()],
  "Thng ny": [moment().startOf("month"), moment().endOf("month")],
  "Thng trc": [
    moment().subtract(1, "month").startOf("month"),
    moment().subtract(1, "month").endOf("month"),
  ],
};

