import React, { useEffect, useState, useRef } from "react";
import ABreadcrumb from "Components/Admin/Breadcrumb/Breadcrumb";
import "./OrderDetail.scss";
import { useDispatch } from "react-redux";
import { getOrderByIdAction, updateOrderAction } from "state/actions/index";
import { formatDateTime } from "utils/helper";
import { Timer } from "pages/Admin/Orders/Orders";
import OutsideClickWrapper from "Components/Admin/OutsideClickWrapper/OutsideClickWrapper";
import { useReactToPrint } from "react-to-print";
import ProductToPrint from "pages/Admin/Orders/ProductToPrint";
import { Link } from "react-router-dom";
import { Verify, Warning } from "Components/Admin/Svg/index";
import swal from "sweetalert";
import { vietNamCurrency, formatCurrency } from "utils/index";

const WAITING = 1;
const CONFIRM = 2;
const CANCEL = 3;

function AOrderDetail({
  match: {
    params: { id },
  },
}) {
  const [order, setOrder] = useState({});
  const [dropdown, setDropdown] = useState(false);
  const [txtNote, setNote] = useState("");

  const dispatch = useDispatch();
  const componentRef = useRef();

  useEffect(() => {
    dispatch(getOrderByIdAction({ id })).then((r) => {
      const newOrder = {
        id: r.Id,
        address: r.DeliverAddress,
        createDate: r.OrderDate,
        confirmDate: r.ConfirmDate,
        cancelDate: r.CancelDate,
        deliveryDate: r.DeliveryDate,
        beginDelivery: r.BeginDelivery,
        receiverName: r.RecipientName,
        phone: r.RecipientPhoneNumber,
        status: r.Status,
        total: r.Total,
        customerId: r.CustomerId,
        customerName: r.Customer.Name,
        customerEmail: r.Customer.Email,
        items:
          r.OrderItems.map((i) => ({
            id: i.ShoesId,
            name: i.ShoesName,
            img: i.ImagePath,
            price: i.PricePerUnit,
            amount: i.Amount,
            total: i.Total,
          })) || [],
        note: r.Note,
        totalAmount: r.OrderItems.reduce(
          (accumulator, currentValue) => accumulator + currentValue.Amount,
          0
        ),
        totalPrice: r.OrderItems.reduce(
          (accumulator, currentValue) => accumulator + currentValue.Total,
          0
        ),
      };

      setNote(r.Note);
      setOrder(newOrder);
    });

    const tx = document.getElementById("order--note");
    tx.setAttribute(
      "style",
      "height:" + tx.scrollHeight + "px;overflow:hidden;"
    );
    tx.addEventListener("input", onInput, false);
  }, []);

  const {
    createDate,
    status,
    deliveryDate,
    beginDelivery,
    total,
    items,
    customerName,
    receiverName,
    phone,
    address,
    totalAmount,
    totalPrice,
    customerId,
    customerEmail,
    confirmDate,
    cancelDate,
    note,
  } = order;

  const formatStatus = (stt) =>
    stt === 1 ? "Ch xc nhn" : stt === 2 ? " xc nhn" : " hu";

  const formatDelivery = () =>
    deliveryDate
      ? " giao"
      : beginDelivery
      ? "ang giao hng"
      : "Cha giao hng";

  const delivery = deliveryDate
    ? "delivered"
    : beginDelivery
    ? "delivering"
    : "not-yet";

  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    documentTitle: "Danh sch n hng",
  });

  const BREADCRUMB = [
    { link: "/admin/orders", name: "Danh sch n hng" },
    { link: `/admin/orders/${id}`, name: `#${id}` },
  ];

  const props = {
    createDate,
    total,
    items,
    id,
    customerName,
    receiverName,
    phone,
    address,
  };

  const onInput = (e) => {
    e.target.style.height = "auto";
    e.target.style.height = e.target.scrollHeight + "px";
  };

  const details = [
    { label: "S lng sn phm", value: totalAmount, post: "" },
    { label: "Tng tin hng", value: totalPrice, post: "" },
    { label: "Gim gi", value: 0, post: "" },
    { label: "Vn chuyn", value: 0, post: "" },
    {
      label: "Tng gi tr n hng",
      value: total,
      post: "",
      sub: "Thanh ton khi giao hng (COD)",
    },
    {
      label: " thanh ton",
      value: deliveryDate ? totalPrice : 0,
      post: "",
    },
  ];

  const updateOrder = (order, res) => ({
    ...order,
    status: res.Status,
    deliveryDate: res.DeliveryDate,
    beginDelivery: res.BeginDelivery,
    cancelDate: res.CancelDate,
    confirmDate: res.ConfirmDate,
    note: res.Note,
  });

  const handleUpdateOrder = (
    id,
    status,
    deliveryDate,
    beginDelivery,
    cancelDate,
    confirmDate,
    note,
    confirm,
    msg,
    icon
  ) => {
    if (confirm) {
      swal(msg, {
        buttons: ["Tr li", "Tip tc"],
        icon,
      }).then((cancel) => {
        if (cancel) {
          dispatch(
            updateOrderAction({
              id,
              status,
              deliveryDate,
              beginDelivery,
              cancelDate,
              confirmDate,
              note,
            })
          ).then((res) => {
            let newOrder = { ...order };
            newOrder = updateOrder(newOrder, res);
            setOrder(newOrder);
          });
        }
      });
    } else {
      dispatch(
        updateOrderAction({
          id,
          status,
          deliveryDate,
          beginDelivery,
          cancelDate,
          confirmDate,
          note,
        })
      ).then((res) => {
        let newOrder = { ...order };
        newOrder = updateOrder(newOrder, res);
        setOrder(newOrder);
      });
    }
  };

  const handleDelivery = (e) => {
    if (e.target.className.includes("disabled")) return;

    const now = new Date().toISOString();
    handleUpdateOrder(
      parseInt(id),
      status,
      deliveryDate,
      now,
      cancelDate,
      confirmDate,
      note,
      true,
      `Xc nhn giao n hng #${id}`,
      "info"
    );
  };

  const handleDelivered = (e) => {
    if (e.target.className.includes("disabled")) return;
    const now = new Date().toISOString();

    handleUpdateOrder(
      parseInt(id),
      status,
      now,
      beginDelivery,
      cancelDate,
      confirmDate,
      note,
      true,
      `Xc nhn giao thnh cng n hng #${id}`,
      "info"
    );
  };

  const handleCancel = (e) => {
    if (e.target.className.includes("disabled")) return;

    const now = new Date().toISOString();
    handleUpdateOrder(
      parseInt(id),
      3,
      deliveryDate,
      beginDelivery,
      now,
      confirmDate,
      note,
      true,
      "Trong trng hp n hng gi mo, khch hng thay i nhu cu hoc sn phm  ht hng, bn nn hu n hng. Hu b n hng l thao tc khng th phc hi li",
      "warning"
    );
  };

  const handleConfirm = (e) => {
    if (e.target.className.includes("disabled")) return;

    const now = new Date().toISOString();
    handleUpdateOrder(
      parseInt(id),
      2,
      deliveryDate,
      beginDelivery,
      cancelDate,
      now,
      note,
      true,
      `Xc nhn n hng #${id}`,
      "info"
    );
  };

  const handleSaveNote = () => {
    handleUpdateOrder(
      parseInt(id),
      status,
      deliveryDate,
      beginDelivery,
      cancelDate,
      confirmDate,
      txtNote,
      false
    );
  };

  return (
    <div className="order-detail--wrapper">
      <ABreadcrumb title={`n hng #${id}`} list={BREADCRUMB} />
      <div className="order-detail--header">
        <div className="row align-items-start">
          <div className="col">
            <div className="order-detail--list-status">
              <div className="order-detail--info">
                <div className="order-detail--info--label pl-0">M</div>
                <div className="order-detail--info--detail">
                  <div className="table-break-word">
                    <strong className="order-detail--info--detail-name">
                      {`#${id}`}
                    </strong>
                  </div>
                </div>
              </div>
              <div className="order-detail--info">
                <div className="order-detail--info--label pl-0">Ngy to</div>
                <div className="order-detail--info--detail">
                  <div className="table-break-word">
                    <span className="order-detail--info--detail-name">
                      {formatDateTime(new Date(createDate))}
                    </span>
                  </div>
                </div>
              </div>
              <div className="order-detail--info">
                <div className="order-detail--info--label pl-0">
                  Trng thi n hng
                </div>
                <div className="order-detail--info--detail">
                  <div className="table-break-word">
                    <span className="order-detail--info--detail-name">
                      <div className="status--component">
                        <span
                          className={`circle--status mr-2 circle--status--${status}`}
                        ></span>
                        <span className={`badges--status--${status}`}>
                          {formatStatus(status)}
                        </span>
                      </div>
                    </span>
                  </div>
                </div>
              </div>
              {status < CANCEL && (
                <div className="order-detail--info">
                  <div className="order-detail--info--label pl-0">
                    Trng thi giao hng
                  </div>
                  <div className="order-detail--info--detail d-flex">
                    <div className="table-break-word">
                      <span className="order-detail--info--detail-name">
                        <div className="status--component">
                          <span
                            className={`circle--status mr-2 circle--status--${delivery}`}
                          ></span>
                          <span className={`badges--status--${delivery}`}>
                            {formatDelivery()}
                          </span>
                        </div>
                      </span>
                    </div>
                    {deliveryDate && (
                      <span className="d-inline-block ml-3">
                        <Timer />
                        <span>{formatDateTime(new Date(deliveryDate))}</span>
                      </span>
                    )}
                  </div>
                </div>
              )}
            </div>
          </div>
          <div className="col-auto">
            <OutsideClickWrapper
              className="d-inline-block ml-3 mt-2"
              onClickOutside={() => setDropdown(false)}
              isShowing={dropdown}
            >
              <div
                className={`dropdown dd-arrow-right dd-placement-right ${
                  dropdown ? "dd-popover--contains" : ""
                }`}
              >
                <div>
                  <button
                    className="btn btn-clean btn-fix-height btn-bg"
                    onClick={() => setDropdown(!dropdown)}
                  >
                    <span className="mr-2">Thao tc</span>
                    <i
                      className="icon-arrow-down"
                      style={{ fontSize: "10px" }}
                    />
                  </button>
                </div>
                <div
                  className={`dd-popover ${dropdown ? "dd-popover--show" : ""}`}
                >
                  <div className="dd-popover__tooltip"></div>
                  <div className="dd-popover__content-wrapper">
                    <div
                      className="dd-popover__content"
                      style={{ width: "160px" }}
                    >
                      <div className="dd-popover__pane">
                        <div className="my-2">
                          <p
                            className={`ellipsis-item ${
                              status === CONFIRM
                                ? beginDelivery
                                  ? "disabled"
                                  : ""
                                : "disabled"
                            }`}
                            onClick={handleDelivery}
                          >
                            Bt u giao hng
                          </p>
                          <p
                            className={`ellipsis-item ${
                              status === CONFIRM
                                ? beginDelivery
                                  ? !deliveryDate
                                    ? ""
                                    : "disabled"
                                  : "disabled"
                                : "disabled"
                            }`}
                            onClick={handleDelivered}
                          >
                            Hon tt giao hng
                          </p>
                          <p
                            className={`ellipsis-item ${
                              status < CANCEL ? "" : "disabled"
                            }`}
                            onClick={handleCancel}
                          >
                            Hu n hng
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </OutsideClickWrapper>
            <div className="d-inline-block ml-3 mt-2">
              <button className="btn btn-clean btn-bg" onClick={handlePrint}>
                <span>
                  <i
                    className="icon-printer mr-2"
                    style={{ fontSize: "16px" }}
                  />
                  <span>In</span>
                </span>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div style={{ padding: "30px 0", margin: "0 auto" }}>
        {status === CANCEL && (
          <div className="row">
            <div className="col-lg-9 col-12">
              <div className="card mb-5">
                <div className="ss-alert ss-alert-warning ss-alert-has-icon ss-alert-with-description">
                  <div className="ss-alert-icon">
                    <Warning />
                  </div>
                  <div className="ss-alert-message">
                    <div className="mt-1">Hy n hng</div>
                  </div>
                  <div className="ss-alert-description">
                    <div className="row" style={{ paddingTop: "10px" }}>
                      <div className="col-auto">
                        <span>
                          {`n hng  c hy vo lc `}
                          <strong>
                            {formatDateTime(new Date(cancelDate))}
                          </strong>
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
        <div className="row">
          <div className="col-lg-9 col-12">
            <div className="card mb-5 position-relative">
              <div style={{ padding: "16px" }}>
                <div className="content--head">
                  <div className="d-flex">
                    <span className="content--title">Danh sch sn phm</span>
                  </div>
                </div>
                <div className="content--body">
                  <div style={{ marginTop: "16px" }}>
                    <div className="table--container">
                      <table className="ui-table ui-table-vertical-top">
                        <thead>
                          <tr>
                            <th className="table-header--product"></th>
                            <th className="text-center table-header--amount">
                              S lng
                            </th>
                            <th className="text-right table-header--amount">
                              Gi ()
                            </th>
                            <th className="text-right table-header--amount">
                              Thnh tin ()
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {items &&
                            items.map((item, index) => (
                              <Product {...item} key={index} />
                            ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="card mb-5">
              <div style={{ padding: "16px" }}>
                <div className="content--body">
                  <div className="form-group px-0 pb-0">
                    <div className="row">
                      <div className="col-sm-6">
                        <div style={{ marginBottom: "15px" }}>
                          <label className="title-level--sub">
                            Ghi ch n hng
                          </label>
                          <textarea
                            value={txtNote}
                            onChange={(e) => setNote(e.target.value)}
                            id="order--note"
                            placeholder="Thm ghi ch cho n hng"
                            rows={1}
                            className="table-textarea"
                          ></textarea>
                        </div>
                        <div style={{ marginBottom: "15px" }}>
                          <button
                            className="btn btn-primary"
                            onClick={handleSaveNote}
                          >
                            <span>Cp nht</span>
                          </button>
                        </div>
                      </div>
                      <div className="col-sm-6">
                        {details.map((detail, index) => (
                          <Row {...detail} key={index} />
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-3 col-12">
            {status < CANCEL && (
              <div className="card mb-5">
                <div style={{ padding: "16px" }}>
                  <div className="content--head">
                    <span className="content--title mb-0">
                      Xc nhn n hng
                    </span>
                  </div>
                  <div className="content--body">
                    <div className="form-group px-0 pb-0">
                      {status === WAITING && (
                        <>
                          <div style={{ marginBottom: "16px" }}>
                            Vui lng xc nhn n hng
                          </div>
                          <button
                            className="btn btn-primary w-100"
                            onClick={handleConfirm}
                          >
                            Xc nhn n hng
                          </button>
                        </>
                      )}
                      {status === CONFIRM && (
                        <>
                          <Verify />
                          <span>n hng  c xc nhn</span>
                        </>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            )}
            <div className="card mb-5">
              <div style={{ padding: "16px" }}>
                <div className="content--head">
                  <span className="content--title mb-0">
                    Thng tin ngi mua
                  </span>
                </div>
                <div className="content--body">
                  <div className="form-group px-0 pb-0">
                    <Link
                      className="mb-2 d-block"
                      target="_blank"
                      to={`/admin/customer/${customerId}`}
                    >
                      {customerName}
                    </Link>
                    <p className="mb-0 color-gray-solid table-break-word">
                      {customerEmail}
                    </p>
                  </div>
                </div>
                <div className="content--body" style={{ paddingTop: "16px" }}>
                  <div className="row no-gutters align-item-center">
                    <div className="col">
                      <p className="mb-0 content--title">Thng tin giao hng</p>
                    </div>
                  </div>
                  <div
                    className="form-group px-0 pb-0"
                    style={{ marginBottom: 0 }}
                  >
                    <p className="mb-0 color-gray-solid table-break-word">
                      Ngi nhn:
                    </p>
                    <p className="mb-0 text-black">{receiverName}</p>
                  </div>
                  <div
                    className="form-group px-0 pb-0"
                    style={{ marginBottom: 0 }}
                  >
                    <p className="mb-0 color-gray-solid table-break-word">
                      S in thoi:
                    </p>
                    <p className="mb-0 text-black">{phone}</p>
                  </div>
                  <div
                    className="form-group px-0 pb-0"
                    style={{ marginBottom: 0 }}
                  >
                    <p className="mb-0 color-gray-solid table-break-word">
                      a ch nhn:
                    </p>
                    <p className="mb-0 text-black">{address}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="d-none">
        <ProductToPrint ref={componentRef} {...props} />
      </div>
    </div>
  );
}

export default AOrderDetail;

const Product = ({ id, name, img, price, amount, total }) => (
  <tr>
    <td className="align-middle text-normal fix-overflow-tooltip">
      <div className="order-block--table">
        <div className="order-block--table-image">
          <div className="table-cell--image">
            <img className="box-image" src={img} alt="product" />
          </div>
        </div>
        <div className="order-block--table-content">
          <Link target="_blank" to={`/admin/shoes/${id}`}>
            <strong className="text-primary mb-2 d-inline-block">{name}</strong>
          </Link>
          <div className="mb-2 text-secondary">Mu: Hng, Size: 40</div>
        </div>
      </div>
    </td>
    <td className="align-middle text-center">
      <div className="border-0">{amount}</div>
    </td>
    <td className="align-middle text-right">
      <div className="border-0 text-right">{`${vietNamCurrency(price)}`}</div>
    </td>
    <td className="align-middle text-right">
      <div className="border-0 text-right">{`${vietNamCurrency(total)}`}</div>
    </td>
  </tr>
);

const Row = ({ label, value, post, sub }) => (
  <div style={{ paddingBottom: "10px", fontSize: "14px" }}>
    <div className="row">
      <div className="col">
        {!sub ? (
          label
        ) : (
          <>
            <p className="mb-1" style={{ fontWeight: "bold" }}>
              {label}
            </p>
            <p className="text-secondary mb-0">{sub}</p>
          </>
        )}
      </div>
      <div className="col-auto text-right">{`${
        value ? formatCurrency(value) : value
      } ${post}`}</div>
    </div>
  </div>
);
