import React, { Component } from "react";
import ABreadcrumb from "Components/Admin/Breadcrumb/Breadcrumb";
import "./Promotion.scss";
import { Button } from "react-bootstrap";
import history from "state/history";
class APromotion extends Component {
  render() {
    return (
      <div className="APromotion">
        <ABreadcrumb title="Danh sch khuyn mi" list={BREADCRUMB} />
        <div className="ui-empty-state_container max-width-center">
          <div className="row align-items-center">
            <div className="col-xs-12 col-md-6">
              <h3 className="ui-empty-state_title">Chng trnh khuyn mi</h3>
              <h4 className="ui-empty-state_subtitle">
                To ra cc m coupon, ci t mt sn phm, hoc mt nhm sn
                phm khuyn mi dnh cho cc khch hng thn thit ca bn.
              </h4>
              <div className="ui-empty-state_btngroup">
                <Button
                  onClick={() => history.push("/admin/promotion/add")}
                  className="fa fa-plus-circle btn-primary"
                  type="button"
                >
                  To khuyn mi
                </Button>
              </div>
            </div>
            <div className="col-xs-12 col-md-6 pt-2">
              <img
                src="//hstatic.net/0/0/global/design/omni/discounts-init-v3.svg"
                alt="background"
              ></img>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const BREADCRUMB = [{ link: "/admin/promotion", name: "Danh sch khuyn mi" }];

export default APromotion;
